            <button
              class="nav-link"
              data-bs-toggle="tab"
              type="button"
              :id="`head-${tab.component}`"
              :data-bs-target="`#${tab.component}`"
              :class="{ active: index == 0 }"
              v-html="tab.name"
            ></button>
          </li>
        </ul>

        <div class="tab-content" id="mainContent">
          <div
            v-for="(tab, index) of tabs"
            class="tab-pane fade px-2 p-lg-0"
            :id="tab.component"
            :key="tab.component"
