            </div>
            <div class="container-fluid mb-2 fixed-bottom g-2 mx-0">
              <button
                type="button"
                class="btn btn-danger w-100"
                data-bs-dismiss="offcanvas"
                aria-label="Close"
                v-on:touchstart.passive="saveCalc"
              >
