<script>
import { mapActions } from "vuex";

import Materials from "./components/Materials.vue";
import Products from "./components/Products.vue";
import Logistics from "./components/Logistics.vue";

import Result from "./components/Result.vue";

export default {
  name: "App",
  data() {
    return {
      tabs: [
        {
          component: "materials",
          name: "Материал",
        },
        {
          component: "products",
          name: "Изделия",
        },
        {
          component: "logistics",
          name: "Логистика",
        },
      ],
    };
  },
  methods: {
    ...mapActions(["save"]),
    focusCard(tab, id) {
      setTimeout(function () {
        let element = document.getElementById(`${tab}-${id}`);
        element.scrollIntoView({
          block: "center",
          behavior: "auto",
        });
      }, 0);
    },
  },
  components: {
    Materials,
    Products,
    Logistics,
    Result,
  },
};
</script>

