<template>
  <div>
    <material-card
      class="material-block"
      v-for="(card, index) of cards"
      :card_index="index"
      :key="index"
      :id="'material-card-' + cardIndex"
      @remove="removeMaterialCard(index)"
    >
    </material-card>
    <div class="button-row row justify-content-center mt-3">
      <button type="button" class="btn btn-primary col-6" @click="newCard">
        +
      </button>
    </div>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
import MaterialCard from "./MaterialCard.vue";

export default {
  name: "Material",
  emits: ["focusCard"],
  computed: {
    cards() {
      return this.$store.state.values.material_cards;
    },
  },
  methods: {
    ...mapMutations(["addMaterialCard", "removeMaterialCard"]),
    newCard() {
      this.addMaterialCard();
      let id = this.cards.length;
      this.$emit("focusCard", "material-card", id - 1);
    },
  },
  components: {
    MaterialCard,
  },
};
</script>

<style>
.material-block:nth-child(odd) {
  background-color: peachpuff;
}
.material-block:nth-child(even) {
  background-color: azure;
}
</style>