<template>
  <div class="list-group list-group-flush">
    <div class="list-group-item">
      <h4 class="mb-1">Материал</h4>
      <ul>
        <li
          v-for="(material, index) of materials"
          :key="index"
          class="row justify-content-between g-0 mb-2"
        >
          <span class="col h6 my-auto" v-if="material.name">{{
            material.name
          }}</span>
          <span class="col my-auto" v-else>Кварцевый агломерат</span>
          <span class="col text-end my-auto"
            >{{
              material.price + overPrice.result / materials.length
            }}&nbsp;руб.</span
          >
        </li>

        <li class="row justify-content-between g-0 mt-2">
          <span class="col h6 my-auto">Материалы / расходники</span>
          <span class="col text-end my-auto"
            >{{ measurementPrice.result }}&nbsp;руб.</span
          >
        </li>

        <li
          class="row justify-content-between g-0 mt-2"
          v-if="materialSum != 0"
        >
          <span class="col my-auto fw-light">Подытог:</span>
          <span class="col text-end my-auto fw-bold"
            >{{
              materialSum.price + measurementPrice.result + overPrice.result
            }}&nbsp;руб.</span
          >
        </li>
      </ul>
    </div>
    <div class="list-group-item" v-if="products.length">
      <h4 class="mb-1">Изделия</h4>
      <ul>
        <li
          v-for="(product, product_index) of products"
          :key="product_index"
          class="row justify-content-between g-0 mb-2"
        >
          <span class="col my-auto">{{ product.name }}</span>
          <span class="col text-end my-auto"
            >{{
              optionSum[product_index].result + addonSum[product_index].result
            }}&nbsp;руб.</span
          >
          <ul class="col-12">
            <li
              class="small-text row justify-content-between g-0 mb-2"
              v-for="(option, option_index) of product.options"
              :key="option_index"
            >
              <span class="col my-auto">{{ option.text }}</span>
              <span class="col text-end my-auto"
                >{{ option.result }}&nbsp;руб.</span
              >
            </li>
            <li
              class="small-text row justify-content-between g-0 mb-2"
              v-for="(addon, addon_index) of addons[product_index]"
              :key="addon_index"
            >
              <span class="col my-auto">{{ addon.name }}</span>
              <span class="col text-end my-auto"
                >{{ addon.result }}&nbsp;руб.</span
              >
            </li>
          </ul>
        </li>
        <li
          class="row justify-content-between g-0 mt-2"
          v-if="optionSum.length > 1"
        >
          <span class="col my-auto fw-light">Подытог:</span>
          <span class="col text-end my-auto fw-bold"
            >{{ productSum.result }}&nbsp;руб.</span
          >
        </li>
      </ul>
    </div>
    <div class="list-group-item" v-if="logisticsList.length">
      <h4>Логистика</h4>
      <ul>
        <li
          v-for="(logistic, index) of logisticsList"
          :key="index"
          class="row justify-content-between g-0 mb-2"
        >
          <span class="col h6 my-auto">{{ logistic.text }}</span>
          <span class="col text-end my-auto"
            >{{ logistic.result }}&nbsp;руб.</span
          >
        </li>

        <li
          class="row justify-content-between g-0 mt-2"
          v-if="logisticSum.result != 0"
        >
          <span class="col my-auto fw-light">Подытог:</span>
          <span class="col text-end my-auto fw-bold"
            >{{ logisticSum.result }}&nbsp;руб.</span
          >
        </li>
      </ul>
    </div>
    <div class="list-group-item">
      <h4>Итог: {{ total }}</h4>
      <div class="btn-group w-100" role="group">
        <input
          type="radio"
          class="btn-check"
          name="discountRadio"
          id="discount0"
          value="0"
          autocomplete="off"
          v-model="discount"
        />
        <label class="btn btn-outline-primary" for="discount0"
          >Без скидки</label
        >

        <input
          type="radio"
          class="btn-check"
          name="discountRadio"
          id="discount10"
          value="10"
          autocomplete="off"
          v-model="discount"
        />
        <label class="btn btn-outline-primary" for="discount10"
          >Колонка 1</label
        >

        <input
          type="radio"
          class="btn-check"
          name="discountRadio"
          id="discount20"
          value="20"
          autocomplete="off"
          v-model="discount"
        />
        <label class="btn btn-outline-primary" for="discount20"
          >Колонка 2</label
        >

        <input
          type="radio"
          class="btn-check"
          name="discountRadio"
          id="discount30"
          value="30"
          autocomplete="off"
          v-model="discount"
        />
        <label class="btn btn-outline-primary" for="discount30"
          >Колонка 3</label
        >
      </div>
    </div>
    Траты: {{ spendings }} Рекомендуемая колонка: {{ recommendedDiscount }}
  </div>
</template>

<script>
export default {
  computed: {
    materials() {
      return this.$store.getters.materials;
    },
    materialSum() {
      return this.materials.length
        ? this.materials.reduce((x, y) => ({
            price: x.price + y.price,
            spendings: x.spendings + y.spendings,
          }))
        : 0;
    },
    products() {
      return this.$store.getters.products;
    },
    optionSum() {
      return this.products.map((product, index) =>
        product.options.length > 0
          ? this.products[index].options.reduce((x, y) => ({
              result: x.result + y.result,
              salary: x.salary + y.salary,
              consumables: x.consumables + y.consumables,
            }))
          : { result: 0, salary: 0 }
      );
    },
    productSum() {
      return this.optionSum.length > 0
        ? this.optionSum.reduce((x, y) => ({
            result: x.result + y.result,
            salary: x.salary + y.salary,
            consumables: x.consumables + y.consumables,
          }))
        : {
            result: 0,
            salary: 0,
            consumables: 0,
          };
    },
    installation() {
      return this.$store.getters.installationPrice;
    },
    delivery() {
      return this.$store.getters.deliveryPrice;
    },
    additionalWork() {
      return {
        result: this.$store.state.values.logistics.additionalWork,
        text: "Дополнительные работы",
      };
    },
    lifting() {
      return this.$store.getters.manualLifting;
    },
    templates() {
      return {
        result: this.$store.state.values.logistics.templatePrice,
        text: "Изготовление шаблонов",
      };
    },
    logisticsList() {
      return [
        this.lifting,
        this.additionalWork,
        this.delivery,
        this.installation,
        this.templates,
      ].filter((price) => price.result);
    },
    logisticSum() {
      return this.logisticsList.length > 0
        ? this.logisticsList.reduce((x, y) => ({
            result: x.result + y.result,
            salary: x.salary + y.salary,
          }))
        : {
            result: 0,
            salary: 0,
          };
    },
    measurementPrice() {
      return this.$store.getters.measurementPrice;
    },
    overPrice() {
      return {
        result: Number(this.$store.state.values.logistics.overPrice),
      };
    },
    addons() {
      return this.$store.getters.addons;
    },
    addonSum() {
      return this.addons.map((addon) => {
        if (addon.length > 0) {
          let res = addon.reduce((x, y) => {
            return {
              result: x.result + y.result,
              salary: x.salary + y.salary,
            };
          });
          res.result = Math.ceil(res.result * (1 - this.discount / 100));
          return res;
        } else {
          return {
            result: 0,
            salary: 0,
          };
        }
      });
    },
    total() {
      return [
        this.materialSum.price,
        this.measurementPrice.result,
        this.overPrice.result,
        this.productSum.result,
        this.logisticSum.result,
      ].reduce((x, y) => x + y);
    },
    discount: {
      get() {
        return typeof this.$store.state.values.discount == "undefined"
          ? 0
          : this.$store.state.values.discount;
      },
      set(value) {
        this.setDiscount(value);
      },
    },
    spendings() {
      let material_spendings = this.materialSum.spendings;
      let product_spendings =
        this.productSum.salary +
        this.productSum.consumables +
        (this.addonSum.length
          ? this.addonSum.reduce((x, y) => ({ salary: x.salary + y.salary }))
              .salary
          : 0);
      let logistic_spendings =
        this.logisticSum.salary + this.measurementPrice.salary;
      return material_spendings + product_spendings + logistic_spendings;
    },
    recommendedDiscount() {
      const discount = () => {
        let delta = this.spendings / this.total;
        if (delta >= 0.9) {
          return "Без скидки";
        } else if (delta >= 0.8) {
          return 1;
        } else if (delta >= 0.75) {
          return 2;
        } else if (delta < 0.75) {
          return 3;
        }
      };
      return discount();
    },
  },
  methods: {
    setDiscount(value) {
      this.$store.commit("setDiscount", value);
    },
  },
};
</script>

<style>
.small-text {
  font-size: 0.8em;
}
ul {
  padding-left: 1rem !important;
}
</style>